
.cardContainer h2 {
    font-family: 'PT Sans';
    text-shadow: 2px 2px 3px black;
    font-size: 1rem;
    font-weight: 400;
    margin: 10px;
    padding: 10;
    color: #42ea0f;
    z-index: 10;
}

.keyId {
    z-index: 11;
    margin: 10px;
    padding: 10;
    text-shadow: 2px 2px 3px black;
}

.botonX {
    background-color: #a645b5;
    background-image: linear-gradient(315deg, #c966aa 0%, #0dd366 74%);
    border: none;
    z-index: 1;
    position: relative; /* Añadido */
    border-radius: 10px; /* Ajustado */
    align-items: right;
    cursor: pointer;
}

.cardContainer { 
    position: relative;
    z-index: 1;
    display: inline-block;
    width: 200px;
    height: 400px;
    background-color: rgba(95, 13, 103, 0.5); 
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(146, 77, 146, 0.2);
    margin: 10px;
    overflow: hidden;
    position: relative;
    transition: all 0.3s ease-in-out;
    cursor: pointer;
    background-size: 40px 40px;
}

.cardContainer img {
    z-index: -1;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 5px;
    
    box-shadow: 0 0 20px #ea05cf, 0 0 40px #e80be8, 0 0 30px #b01c9e, 0 0 10px #ea05d7;
}

.keyId {
    padding: 10;
}
