.SearchBar {
    position: right;
   align-content: right;
   align-items: right;
}

.inputS {
    text-decoration: none;
    color: black;
    font-size: 1.5rem;
    font-weight: 100;
    border-radius: 10px;
    transition: all 0.3s ease;
    cursor: text;
}

.buttonS {
    background-color: #a645b5;
    background-image: linear-gradient(315deg, #c966aa 0%, #0dd366 74%);
    border: none;
    z-index: 1;
    position: relative; /* Añadido */
    border-radius: 10px; /* Ajustado */
}

.buttonS:hover:after {
    top: -5px;
    left: -5px;
    width: calc(100% + 10px);
    height: calc(100% + 10px);
    border-radius: 20px;
    box-shadow:
    -5px -5px 15px 0px #fff9,
    -2px -2px 5px 0px #fff9,
    5px 5px 15px 0px #0002,
    2px 2px 5px 0px #0001;
}

.buttonS:active {
    top: 2px;
}
.buttonS { 
    font-family: 'PT Sans';
    text-decoration: none;
    color: black;
    font-size: 1.5rem;
    font-weight: 600;
    padding: 0 10px;
    border-radius: 10px;
    transition: all 0.3s ease;
    cursor: pointer;
    margin: 10px;

}

