.nav { 
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background: rgb(0,0,0);
    background: linear-gradient(90deg, rgb(91, 4, 78) 0%, rgb(202, 9, 189) 0%, rgba(22,230,5,1) 99%, rgb(32, 113, 5) 100%, rgba(4,255,0,1) 100%);
    height: 60px;
    border-radius: 50px;
    box-shadow: 0 0 10px 0 rgba(0,0,0,0.5);
}